.site-header {
	position: fixed;
	display: flex;
	justify-content: space-between;
	align-items: center;
	z-index: 105;
	top: 20px;
	padding: 0 40px;
	width: 100%;
	.behind {
		position: absolute;
		top: 10px;
		left: 50%;
		transform: translateX(-50%);
		display: none;
		a {
			color: #fff;
			font-weight: bold;
			@media (min-width: 992px) {
				font-size: 30px; } } } }

.site-header-block {
	display: flex;
	align-items: center;
	position: relative;
	.search-toggle {
		display: block;
		position: relative;

		.search-btn {
			i {
				transform: scaleX(-1);
				&:nth-of-type(2) {
					display: none; } } }
		.search-toggle-el {
			position: absolute;
			top: 50%;
			right: calc(100% + 5px);
			opacity: 0;
			pointer-events: none;
			transform: translate(-10px, -50%);
			visibility: hidden;
			transition: all .3s ease-in-out;
			&:before {
				position: absolute;
				// content: ''
				border: 4px solid transparent;
				border-bottom-color: #fff;
				bottom: 100%;
				right: 26px;
				transform: translateX(50%); }
			form {
				background: #fff;
				border-radius: 20px;
				overflow: hidden;
				display: flex;
				input {
					border: none;
					background: none;
					width: 230px;
					height: 40px;
					padding-left: 20px;
					pointer-events: auto; }
				button {
					background: none;
					border: none;
					padding-right: 15px;
					padding-left: 15px;
					border-radius: 30px;
					&:hover {
						background: rgba(black, .3);
						color: #fff; } } } }
		&.open {
			.search-btn {
				i {
					display: none;
					&:nth-of-type(2) {
						display: block; } } }
			.search-toggle-el {
				opacity: 1;
				transform: translate(0, -50%);
				pointer-events: auto;
				visibility: visible; } } }

	.splash {
		height: 25px;
		width: 1px;
		background: #fff;
		transform: skewX(-25deg);
		margin-left: 20px;
		margin-right: 20px;
		transition: background 1s ease-in-out; }
	.logo {
	 	max-width: 30px;
	 	img {
	 		max-width: 100%; }
	 	a:before {
	 		display: none; } }
	a {
		color: #fff;
		display: block;
		font-weight: bold;
		font-size: 15px;
		position: relative;
		transition: color 1s ease-in-out;
		&:before {
			content: '';
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: 1px;
			background: #fff;
			transform: scale(0);
			transform-origin: top left;
			transition: all .3s ease-in-out; }
		&:hover:before {
			transform: none; } } }

.site-header-block.site-header-right {
	a {
		@media (min-width: 1300px) {
			font-size: 23px; } } }

.site-header._black_bk {
	.behind {
		display: block;
		a {
			color: #333; } }
	.search-toggle .search-toggle-el form {
		background: #f5f5f5; }
	.site-header-block {
		.splash {
			background: #333; }
		a {
			color: #333;
			&:before {
				background: #333; } } } }




@media (min-width: 1366px) {
	.site-header-block {
		a {
			font-size: 20px; } } }

@media (max-width: 991.82px) {
	.site-header {
		top: 10px; } }


@media (max-width: 767.82px) {
	.site-header-block {
		.logo {
			max-width: 25px; } }

	.site-header {
		.behind {
			top: -1px;
			left: 70px;
			transform: none; } } }


@media (max-width: 575.82px) {
	.site-header {
		padding: 0 20px; } }






