@import 'components/fixedThumb';


.ham {
	position: relative;
	width: 30px;
	height: 30px;
	cursor: pointer;
	div:not(.ct) {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 3px;
		transform: translateY(-50%);
		transition: all .3s ease-in-out;
		&:before, &:after {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: #fff;
			transition: all .3s ease-in-out; } }
	&:hover {
		div:nth-of-type(1) {
			&:before, &:after {
				width: 0; } }
		div:nth-of-type(3) {
			&:before, &:after {
				width: 0; } } }
	&.active_ {

		div:not(.ct) {
			&:before, &:after {
				background: #333; }
			&:before {
				top: -7px; }
			&:after {
				top: 7px; } }
		div:nth-of-type(2) {
			&:before, &:after {
				width: 0; } }
		div:first-child {
			top: 50%;
			// transform: translateY(-50%) rotate(90deg)
			background: black; }
		div:nth-of-type(3) {
			top: 50%;
 } }			// transform: translateY(-50%) rotate(90deg)

	div:first-child {
		top: calc(50% - 9px); }
	div:nth-of-type(3) {
		top: calc(50% + 9px); } }



